.faded-popup-visible{
    width: 60%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    visibility: visible;
    visibility: visible;
    opacity: 1;
    transition: visibility 0s linear 0s, opacity 300ms;
}

.faded-popup-invisible{
    width: 60%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 300ms, opacity 300ms;
}