#userForm-wrapper{
    width: 50%;
    background: white;
    position: fixed;
    right: 0;
    left: 0;
    top: 30%;
    margin-right: auto;
    margin-left: auto;
    z-index: 3;
    padding: 20px;
}

#userForm-wrapper p{
    text-align: left;
}

input{
    height: 25px;
    width: 90%;
    border-radius: 5px;
    border: 1px solid #cacaca;
    padding: 5px 10px 5px 10px;
    margin: 0 auto;
}

input:focus{
    outline-offset: 0;
}

.submit-button{
    margin-top: 15px;
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
    border-radius: 4px;
    font-weight: 400;
    text-align: center;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.submit-button:hover{
    color: #fff;
    background-color: #218838;
    border-color: #1e7e34;
    cursor: pointer;
}

.submit-button:active{
    text-decoration: none;
}

.submit-button:focus {outline:0;}