.dropDown-popup-visible{
    width: 80%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    margin: 0 auto;
    transform: translate(-50);
    margin-top: 20px;
    display: inherit;
}

.dropDown-popup-invisible{
    width: 80%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    margin: 0 auto;
    transform: translate(-50);
    margin-top: 20px;
    display: none;
}