html, body {
  margin: 0;
  padding: 0;
  font-family: 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}

input:focus {
  outline: none;
  padding-left: 10px;
  padding-right: 10px;
}

@font-face {
  font-family: Newsense;
  src: url(/Newsense.otf);
}

.github{
  position: fixed;
  bottom: 10px;
  color: #b7b7b7;
  width: 100%;
}

.App{
  height: 100%;
}

#overlay-grey{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    background: black;
    z-index: 2;
    opacity: 0.7;
}
.dropDown-popup-visible{
    width: 80%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    margin: 0 auto;
    -webkit-transform: translate(-50);
            transform: translate(-50);
    margin-top: 20px;
    display: inherit;
}

.dropDown-popup-invisible{
    width: 80%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    margin: 0 auto;
    -webkit-transform: translate(-50);
            transform: translate(-50);
    margin-top: 20px;
    display: none;
}
#userForm-wrapper{
    width: 50%;
    background: white;
    position: fixed;
    right: 0;
    left: 0;
    top: 30%;
    margin-right: auto;
    margin-left: auto;
    z-index: 3;
    padding: 20px;
}

#userForm-wrapper p{
    text-align: left;
}

input{
    height: 25px;
    width: 90%;
    border-radius: 5px;
    border: 1px solid #cacaca;
    padding: 5px 10px 5px 10px;
    margin: 0 auto;
}

input:focus{
    outline-offset: 0;
}

.submit-button{
    margin-top: 15px;
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
    border-radius: 4px;
    font-weight: 400;
    text-align: center;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.submit-button:hover{
    color: #fff;
    background-color: #218838;
    border-color: #1e7e34;
    cursor: pointer;
}

.submit-button:active{
    text-decoration: none;
}

.submit-button:focus {outline:0;}
#challenge-title{
    color: #ef3e61;
    font-family: "Newsense", sans-serif;
    font-size: 100px;
    width: 100%;
    text-align: center;
}

#input-submit-row{
    display: flex;
    flex-direction: row;
    align-items: center;
}

#input-submit-row input{
    flex: 5 1;
    margin: 5px;
}

#input-submit-row button{
    height: 80px;
    width: 80px;
    margin: 5px;
    border-radius: 100px;
    background: transparent;
    font-size: 50px;
    color: white;
}

#input-submit-row button:hover{
    cursor: pointer;
}

#input-submit-row button:focus{
    outline: 0;
}

.black-background-container{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #000000;
}

h1{
    background: #00af8d;
    color: #fff;
    padding: 10px 0px 10px 0px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0 auto;
}


.colored-text-container{
    text-align: justify;
    width: 80%;
    margin: 0 auto;
    background: #292929;
    padding: 20px;
    border-radius: 4px;
    color: #b7b7b7;
}

.darker-text{
    color: white;
    font-size: 22px;
}

.lighter-text{
    color: #00af8d;
    font-weight: 700;
    letter-spacing: 1px;
}


#answer-form{
    text-align: justify;
    width: 70%;
    margin: 0 auto;
}

#answer-form input{
    width: 100%;
    background: transparent;
    height: 73px;
    border-radius: 4px;
    font-weight: 600;
    box-shadow: none;
    border: 2px solid #00af8d;
    color: #ffcc00;
    font-size: 70px;
    padding: 10px;
    padding-bottom: 20px;
    padding-top: 20px;
}
.faded-popup-visible{
    width: 60%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    visibility: visible;
    visibility: visible;
    opacity: 1;
    transition: visibility 0s linear 0s, opacity 300ms;
}

.faded-popup-invisible{
    width: 60%;
    background: #f2dede;
    margin: 0 auto;
    color: #a94442;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #ebccd1;
    margin-top: 20px;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 300ms, opacity 300ms;
}
.invitation{
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    width: 100%;
    color: white;
    z-index: 3;
    font-size: 2em;
    font-weight: bold;
}

.hacker{
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100vw;
    max-height: 100vh;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

