#answer-form{
    text-align: justify;
    width: 70%;
    margin: 0 auto;
}

#answer-form input{
    width: 100%;
    background: transparent;
    height: 73px;
    border-radius: 4px;
    font-weight: 600;
    box-shadow: none;
    border: 2px solid #00af8d;
    color: #ffcc00;
    font-size: 70px;
    padding: 10px;
    padding-bottom: 20px;
    padding-top: 20px;
}