html, body {
  margin: 0;
  padding: 0;
  font-family: 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}

input:focus {
  outline: none;
  padding-left: 10px;
  padding-right: 10px;
}

@font-face {
  font-family: Newsense;
  src: url(/Newsense.otf);
}

.github{
  position: fixed;
  bottom: 10px;
  color: #b7b7b7;
  width: 100%;
}

.App{
  height: 100%;
}
