.invitation{
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    width: 100%;
    color: white;
    z-index: 3;
    font-size: 2em;
    font-weight: bold;
}

.hacker{
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100vw;
    max-height: 100vh;
}