#challenge-title{
    color: #ef3e61;
    font-family: "Newsense", sans-serif;
    font-size: 100px;
    width: 100%;
    text-align: center;
}

#input-submit-row{
    display: flex;
    flex-direction: row;
    align-items: center;
}

#input-submit-row input{
    flex: 5;
    margin: 5px;
}

#input-submit-row button{
    height: 80px;
    width: 80px;
    margin: 5px;
    border-radius: 100px;
    background: transparent;
    font-size: 50px;
    color: white;
}

#input-submit-row button:hover{
    cursor: pointer;
}

#input-submit-row button:focus{
    outline: 0;
}

.black-background-container{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #000000;
}

h1{
    background: #00af8d;
    color: #fff;
    padding: 10px 0px 10px 0px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0 auto;
}


.colored-text-container{
    text-align: justify;
    width: 80%;
    margin: 0 auto;
    background: #292929;
    padding: 20px;
    border-radius: 4px;
    color: #b7b7b7;
}

.darker-text{
    color: white;
    font-size: 22px;
}

.lighter-text{
    color: #00af8d;
    font-weight: 700;
    letter-spacing: 1px;
}

